#root {
	/* align-items: center; */
	padding: 3em 0 2em 0;
	background-color: rgb(0, 197, 131);
}
body {
	height: 100vh;
	overflow-x: hidden;
	/* overflow: hidden; */
}
.App {
	display: flex;
	/* align-items: center; */
	margin-top:3em;
	max-width: 900px;
	width: 90%;
	color: #fff;
}
.row-item {
	flex-grow: 1;
	margin-right: 2em;
}
.screenshots {
	height: 82vh;
	margin-bottom: -16.7117212249vh;
	width: 53.6853220697vh;
	margin-left: -10.3907074974vh;
	margin-right: -10.3907074974vh;
	overflow: hidden;
	max-width: 100vw;
	object-fit: cover;
}
.logo {
	height: 2.5em;
	width: 2.5em;
	border-radius: 0.2em;
}
.logo-main {
	margin-right: 0.3em;
}
.logoText {
	font-weight: bold;
	font-size: 1.3em;
}

.title-row {
	display: flex;
	align-items: center;
	font-size: 1.5em;
	font-weight: bold;
	margin-bottom: -1em;
}

h1 {
	font-size: 3.5em;
	font-weight: bold;
	margin-block: 0em 0em;
	line-height: 1.5em;
}
h2 {
	margin-block: 0em 0.5em;
	font-size: 1.5em;
	color: #fffc;
}
.border {
	width: 30%;
	height: 2px;
	background-color: #fff;
}
h3 {
	font-size: 1.2em;
	font-weight: bold;
	margin: 0px;
}

.mt-5 {
	margin-top: 5em;
}
.mt-3 {
	margin-top: 1.5em;
}
.fields {
	margin-top: 0.5em;
	width: 80%;
	/* height: 4em; */
	margin-left: -1em;
	display: flex;
	padding: 0.5em 1em;
	border-width: 0px;
	border-radius: 2em;
	background-color: #fff;
	box-shadow: #0003 0em 1.1em 1em;
}
input {
	/* padding: 1.5em 12em 1.5em 1.5em; */
	/* max-width: 100vw; */
	flex-grow: 1;
	/* width: 28em; */
	height: 1.5em;
	border: none;
}
input:focus {
	outline: none;
}
.subscribe {
	/* position: absolute; */
	/* margin: 1em 3em 1em 0; */
	line-height: 1.5em;
	background-color: #0000;
	border-width: 0px;
	margin: -0.5em 0 -0.5em 0.5em;
	padding: 0.5em 0;
	height: 1.5em;
}


.header {
	position: absolute;
	top: 0;
	width: 90vw;
	/* left: 0; */
	left: 5vw;
	height: 3.5em;
	display: flex;
	align-items: center;
	border-bottom: solid #fff 0px;
	/* padding: 0 5em; */
	/* padding: 0 100px; */
}

.f1 {
	flex-grow: 1;
}
.lan {
	padding: 0.5em;
	margin-right: 0.2em;
	border: solid #fff 0.5px;
	font-weight: bold;
	font-size: 1.2em;
	line-height: 1em;
	width: 1.2em;
	text-align: center;
}
.inactivelan:hover {
	background-color: #0001;
	border-color: #fff;
}
.inactivelan {
	border-color: #fffc;
}
.activelan {
	background-color: #fff;
	color: #000;
}



.hdiv {
	width: 2px;
	height: 2em;
	background-color: #fff7;
	margin: 0.5em 1.5em;
}

.sm-ic-outer {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0em 0.1em;
}

.sm-ic {
	height: 1.5em;
	width: 1.5em;
	background-size: cover;
}
a:visited, a, a: {
	color: '#f00'
}




@media screen and (max-width: 768px) {
	.big-only {
		display: none;
	}
	.App {
		flex-direction: column-reverse;
		margin-left: 5vw;
	}
	.scr-outer {
		align-self: center;
	}
	.row-item {
		margin-top: 3em;
	}
	.fields {
		width: calc(90vw - 3em);
		margin-left: 0px;
		border-radius: 1em;
	}
}
@media screen and (min-width: 769px) {
	#root {
		height: calc(100% - 5em);
		display: flex;
		justify-content: center;
	}
}

@media screen and (max-width: 480px) {
	body {
		font-size: 12px;
	}
	.subscribe {
		line-height: 1.9em;
	}
	.header {
		font-size: 16px;
	}
}
@media screen and (min-width: 480px) {
	.subscribe {
		font-size: 0.8333333333333333333333em;
	}
}